*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  -webkit-box-sizing: inherit;
  -moz-box-sizing: inherit;
  box-sizing: inherit;
}

html {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  font-size: 100%;
}

html,
body {
  min-height: 100%;
}

h1 a,
h2 a,
h3 a,
h4 a,
h5 a,
h6 a {
  color: inherit;
}

ul {
  margin: 0;
  padding: 0;
}

ul li {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

i {
  font-style: normal;
}

figure {
  margin: 0;
  padding: 0;
}

.clearfix:after {
  content: "";
  clear: both;
  display: table;
}

a,
a:active,
a:focus {
  text-decoration: none;
  outline: none;
  border: none;
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
}

a:hover {
  text-decoration: none;
  outline: none;
  color: #1F1F1F;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

label {
  color: #666;
  cursor: pointer;
  font-size: 14px;
  font-weight: normal;
}

a:focus,
input:focus,
textarea:focus,
button:focus {
  outline: 0 solid;
}

body {
  font-family: "Jost", sans-serif;
  color: #686868;
  font-size: 1rem;
}

p {
  line-height: 1.75;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Source Serif Pro", serif;
  color: #1F1F1F;
}

button {
  cursor: pointer;
}

button.button {
  background: transparent;
  border: 0px;
  -webkit-transition: 0.3s;
  -o-transition: 0.3s;
  transition: 0.3s;
}

::-moz-selection {
  background: #313131;
  color: #fff;
  text-shadow: none;
}

::selection {
  background: #313131;
  color: #fff;
  text-shadow: none;
}

.shadeBg {
  background-color: #FFEFEC;
}

.colorSecondary, .headerTopBar-lang__list > li a, .header-search__input, .header-search__input::placeholder {
  color: #1F1F1F;
}

.fontPrimary, .headerTopBar-lang__list > li a {
  font-family: "Source Serif Pro", serif;
}

.fontSecondary {
  font-family: "Jost", sans-serif;
}

.wh-100 {
  width: 100%;
  height: 100%;
}

.after-before {
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
}

.hidden-transition, .headerTopBar-lang__dropdown {
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
}

.visible-transition, .headerTopBar-lang__list > li:hover > ul {
  opacity: 1;
  visibility: visible;
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
}

.letter--spacing05, .header-search__input, .header-search__input::placeholder {
  letter-spacing: 0.05em;
}

.bg-centerized, .headerMiddle--bg {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}

svg, path, .icon {
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
}

.border-top, .main-menu__nav {
  border-top: 1px solid rgba(136, 146, 165, 0.25);
}

.headerTopBar__item {
  padding: 5px 20px 5px 0;
  position: relative;
  margin-right: 20px;
}

.headerTopBar__item::after {
  background-color: rgba(118, 129, 151, 0.4);
  position: absolute;
  right: 0;
  top: 0;
  content: "";
  width: 1px;
  height: 100%;
}

.headerTopBar__item:last-child {
  padding-right: 0;
  margin-right: 0;
}

.headerTopBar__item:last-child::after {
  display: none;
}

.headerTopBar__quote:hover > a {
  color: #F07654;
}

.header-notify__icon {
  padding-left: 15px;
}

.header-notify__icon .icon > path {
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
}

.header-notify:hover .icon > path {
  stroke: #F07654;
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  transform: rotate(10deg);
}

.headerMiddle {
  position: relative;
}

.headerMiddle--bg {
  padding: 30px 0;
}

.headerTopBar-lang__list > li {
  position: relative;
  padding: 13px 0;
  cursor: pointer;
}

.headerTopBar-lang__list > li a > i {
  padding-left: 8px;
}

.headerTopBar-lang__dropdown {
  position: absolute;
  left: 0;
  top: 0;
  top: 120%;
  width: 180px;
  padding: 10px 10px;
  background: #fff;
  box-shadow: 0px 20px 60px rgba(73, 21, 13, 0.1);
}

.headerTopBar-lang__dropdown > li > a {
  display: block;
  padding: 10px 15px;
}

.headerTopBar-lang__dropdown > li:hover > a {
  padding-left: 20px;
  color: #F07654;
}

.headerTopBar-lang__list > li:hover > a {
  color: #F07654;
}

.headerTopBar-lang__list > li:hover > ul {
  top: 100%;
}

.header-search {
  position: relative;
}

.header-search__input, .header-search__input::placeholder {
  border: 0;
  background-color: transparent;
  font-family: "Source Serif Pro", serif;
  font-size: 16px;
}

.header-search__button {
  background-color: transparent;
  border: 0;
}

.header-search__button .icon {
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
}

.header-search__button .icon > path {
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
}

.header-search:hover .header-search__button .icon {
  transform: rotate(90deg);
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
}

.header-search:hover .header-search__button .icon > path {
  stroke: #F07654;
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
}

.headerBurgerMenu__button {
  background-color: transparent;
  border: 1px solid transparent;
}

.headerSocial__list {
  display: inline-flex;
}

.headerSocial__list > li {
  margin-left: 25px;
}

.headerSocial__list > li:first-child {
  margin-left: 0;
}

.headerSocial__list > li:hover > a > svg > path {
  stroke: #F07654;
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
}

.main-menu__nav {
  text-align: center;
}

.main-menu__nav > ul > li {
  display: inline-block;
  align-items: center;
  position: relative;
  margin: 0 25px;
}

@media (max-width: 1199px) {
  .main-menu__nav > ul > li {
    margin: 0 15px;
  }
}

.main-menu__nav > ul > li > a {
  font-size: 17px;
  color: #1F1F1F;
  position: relative;
  display: inline-block;
  padding: 10px 0px;
  font-family: "Source Serif Pro", serif;
  padding: 27px 0px;
}

.main-menu__nav > ul > li > a::before {
  position: absolute;
  content: "";
  height: 2px;
  width: 0%;
  background: #F07654;
  left: 100%;
  bottom: 0px;
  transition: 0.6s;
  opacity: 0;
  visibility: hidden;
}

.main-menu__nav > ul > li:hover > a {
  color: #F07654;
}

.main-menu__nav > ul > li:hover > a::before {
  background: #F07654;
  left: 0;
  width: 100%;
  opacity: 1;
  visibility: visible;
  transition: width 0.6s;
}

.main-menu__nav > ul > li > .main-menu__dropdown {
  visibility: hidden;
  transition: all 0.4s cubic-bezier(0.3, 0.02, 0, 0.6);
  opacity: 0;
  z-index: 100;
  position: absolute;
  top: 100%;
  left: 0;
  width: 250px;
  margin: 0;
  background-color: #fff;
  text-align: left;
  box-shadow: 0px 7px 65px 0px rgba(0, 0, 0, 0.03);
  padding: 20px 0 50px;
  transform: translateX(-20px);
  transform-origin: 0 0 0;
}

.main-menu__nav > ul > li > .main-menu__dropdown > li {
  display: block;
  padding: 8px 30px;
  position: relative;
}

.main-menu__nav > ul > li > .main-menu__dropdown > li.has-submenu::after {
  content: "\f105";
  font-family: "Font Awesome 5 Free";
  font-weight: 600;
  vertical-align: middle;
  transform: rotate(0deg);
  display: inline-block;
  padding-left: 6px;
  font-size: 12px;
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  position: absolute;
  right: 30px;
  top: 50%;
  transform: translateY(-45%);
}

.main-menu__nav > ul > li > .main-menu__dropdown > li.has-submenu:hover::after {
  color: #F07654;
}

.main-menu__nav > ul > li > .main-menu__dropdown > li > a {
  display: inline-block;
  color: #1F1F1F;
  font-size: 15px;
  text-transform: capitalize;
  letter-spacing: 0.1em;
  padding: 0px 0;
  position: relative;
  font-family: "Jost", sans-serif;
}

.main-menu__nav > ul > li > .main-menu__dropdown > li > a::before {
  position: absolute;
  content: "";
  width: 0%;
  height: 1px;
  background: #F07654;
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  left: 100%;
  bottom: 0;
}

.main-menu__nav > ul > li > .main-menu__dropdown > li > a:hover {
  color: #F07654;
  margin-left: 10px;
}

.main-menu__nav > ul > li > .main-menu__dropdown > li > a:hover::before {
  left: 0;
  width: 100%;
  opacity: 1;
  visibility: visible;
  transition: width 0.8s ease;
}

.main-menu__nav > ul > li > .main-menu__dropdown > li > ul.submenu {
  visibility: hidden;
  transition: all 0.4s cubic-bezier(0.3, 0.02, 0, 0.6);
  opacity: 0;
  z-index: 100;
  position: absolute;
  top: 0%;
  left: 100%;
  width: 250px;
  margin: 0;
  background-color: #fff;
  text-align: left;
  box-shadow: 0px 7px 65px 0px rgba(0, 0, 0, 0.03);
  padding: 20px 0 50px;
  transform: translateX(-30px);
  transform-origin: 0 0 0;
  margin-left: 8px;
}

.main-menu__nav > ul > li > .main-menu__dropdown > li > ul.submenu > li {
  display: block;
  padding: 8px 30px;
  position: relative;
}

.main-menu__nav > ul > li > .main-menu__dropdown > li > ul.submenu > li > a {
  display: inline-block;
  color: #1F1F1F;
  font-size: 15px;
  text-transform: capitalize;
  letter-spacing: 0.1em;
  padding: 0px 0;
  position: relative;
  font-family: "Jost", sans-serif;
}

.main-menu__nav > ul > li > .main-menu__dropdown > li > ul.submenu > li > a::before {
  position: absolute;
  content: "";
  width: 0%;
  height: 1px;
  background: #F07654;
  opacity: 0;
  visibility: hidden;
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  left: 100%;
  bottom: 0;
}

.main-menu__nav > ul > li > .main-menu__dropdown > li > ul.submenu > li > a:hover {
  color: #F07654;
  margin-left: 10px;
}

.main-menu__nav > ul > li > .main-menu__dropdown > li > ul.submenu > li > a:hover::before {
  left: 0;
  width: 100%;
  opacity: 1;
  visibility: visible;
  transition: width 0.8s ease;
}

.main-menu__nav > ul > li > .main-menu__dropdown > li:hover > ul.submenu {
  opacity: 1;
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  visibility: visible;
  transform: translateX(0px);
}

.main-menu__nav > ul > li:hover .main-menu__dropdown {
  opacity: 1;
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  visibility: visible;
  top: 100%;
  transform: scaleY(1);
}

.main-menu__nav > ul > li:hover .mega_menu {
  transform: scaleY(1);
  transition: all 0.3s cubic-bezier(0.3, 0.02, 0, 0.6);
  transition-delay: 0ms;
}

.main-menu__nav_sub > a::after {
  content: "\f107";
  font-family: "Font Awesome 5 Free";
  font-weight: 600;
  vertical-align: middle;
  transform: rotate(0deg);
  display: inline-block;
  padding-left: 6px;
  font-size: 12px;
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
}

.main-menu__nav_sub:hover > a::after {
  transform: rotate(180deg) translateX(-6px);
  transition: all 0.4s ease;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  transition-delay: 0ms;
}
