/*-- Margin Top --*/
@for $i from 1 through 30 {
    .mt-#{5 * $i} {
      margin-top: 5px * $i;
    }
  }
  
  /*-- Margin Bottom --*/
  @for $i from 1 through 30 {
    .mb-#{5 * $i} {
      margin-bottom: 5px * $i;
    }
  }
  
  /*-- Margin Left --*/
  @for $i from 1 through 30 {
    .ml-#{5 * $i} {
      margin-left: 5px * $i;
    }
  }
  
  /*-- Margin Right --*/
  @for $i from 1 through 30 {
    .mr-#{5 * $i} {
      margin-right: 5px * $i;
    }
  }
  
  /*-- Padding Top --*/
  @for $i from 1 through 30 {
    .pt-#{5 * $i} {
      padding-top: 5px * $i;
    }
  }
  
  /*-- Padding Bottom --*/
  @for $i from 1 through 30 {
    .pb-#{5 * $i} {
      padding-bottom: 5px * $i;
    }
  }
  
  /*-- Padding Left --*/
  @for $i from 1 through 30 {
    .pl-#{5 * $i} {
      padding-left: 5px * $i;
    }
  }
  
  /*-- Padding Right --*/
  @for $i from 1 through 40 {
    .pr-#{5 * $i} {
      padding-right: 5px * $i;
    }
  }
  
  // Font Size
  @for $i from 1 through 200 {
    .fs-#{1 * $i} {
      font-size: 1px * $i;
    }
  }
  
  // Position Vertical Horizontal Vertical
  @mixin center($position) {
    position: absolute;
  
    @if $position == "vertical" {
      top: 50%;
      -webkit-transform: translateY(-50%);
      -ms-transform: translateY(-50%);
      transform: translateY(-50%);
    } @else if $position == "horizontal" {
      left: 50%;
      -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
      transform: translate(-50%);
    } @else if $position == "both" {
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
      transform: translate(-50%, -50%);
    }
  }
  //   @include center(both);
  
  @mixin background-opacity($color, $opacity: 0.3) {
    background: $color;
    /* The Fallback */
    background: rgba($color, $opacity);
  }
  
  // Transition
  @mixin transitionSmooth($property: all, $duration: 0.4s, $ease: ease) {
    transition: $property $duration $ease;
    -webkit-transition: $property $duration $ease;
    -moz-transition: $property $duration $ease;
    -ms-transition: $property $duration $ease;
  }
  
  // absolute-left-top
  @mixin absolute-left-top($position: absolute, $left: 0, $top: 0) {
    position: $position;
    left: $left;
    top: $top;
  }
  
  // absolute-left-bottom
  @mixin absolute-left-bottom($position: absolute, $left: 0, $bottom: 0) {
    position: $position;
    left: $left;
    bottom: $bottom;
  }
  
  // absolute-right-top
  @mixin absolute-right-top($position: absolute, $right: 0, $top: 0) {
    position: $position;
    right: $right;
    top: $top;
  }
  
  // absolute-right-bottom
  @mixin absolute-right-bottom($position: absolute, $right: 0, $bottom: 0) {
    position: $position;
    right: $right;
    bottom: $bottom;
  }
  
  // Heading
  
  // HeadingTypographyOne
  @mixin HeadingTypographyOne($font-size: toEm(80), $line-height: 1.375) {
    font-size: $font-size;
    line-height: $line-height;
  }
